const opreate={
    emitCall(callname, data,vm) {
        vm.$emit(callname,data);
      },
      ObjectCopy: objectcopy,
      //判断是否存在按钮权限
      hasLoginComponents(vm,perm){
        var userperms=vm.$store.getters.GETUSERPERMS;
        var index= userperms.indexOf(perm);
        if(index>-1){
          return true;
        }else{
          return false;
        }
      },
      //检测checkData并返回boolean
      TESTcheck(vm,title){
        var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
        var len = checkData.length;
        if (len > 1) {
          vm.$layer.msg(
            '一次只能'+title+'一条数据',{icon:2}
          );
          return false;
        } else if (len <= 0) {
          vm.$layer.alert(
            '请选择需要'+title+'的数据',{icon:0}
          );
           return false;
        } else {
          return true;
        }
      },
      datafilter(value, type) {
        //为小于10的数字加0
      function addZero(val) {
        if (val < 10) {
          return "0" + val;
        } else {
          return val;
        }
      }
        var date = new Date(Number(value));
        var year = addZero(date.getFullYear());
        var month = addZero(date.getMonth()+1);
        var day = addZero(date.getDate());
        if (type == "yyyyMMdd") {
          return year + "年" + month + "月" + day + "日";
        }
      }
}
function objectcopy(obj) {
    var data;
    if (obj instanceof Object) {
      data = {};
    }
    if (obj instanceof Array) {
      data = [];
    }
    for (var key in obj) {
      if (typeof obj[key] == "object") {
        data[key] = objectcopy(obj[key]);
      } else {
        data[key] = obj[key];
      }
    }
    return data;
  }
export default opreate;


