// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import publiccall from './publicCall'
import permission from './permission'
import axios from 'axios'
import store from './store'
import filters from './filters'
import validate from './Validate'
import VueCookies from 'vue-cookies'
import api from './api'

import jQuery from 'jquery'
window.jQuery = jQuery;
window.$=jQuery;
import layer from 'layui-layer'
Vue.use(VueCookies)
axios.defaults.withCredentials=true;
Vue.config.productionTip = false;
Vue.prototype.$api=api;
Vue.prototype.$layer = layer;
Vue.prototype.HOST="";
// Vue.prototype.HOST="http://192.168.43.49:8081/AI_BD/";
// Vue.prototype.HOST="http://192.168.1.182:8081/AI_BD/";
// Vue.prototype.HOST="http://192.168.1.115:8081/AI_BD/";
// Vue.prototype.HOST="http://192.168.1.109:8081/AI_BD/";
// Vue.prototype.HOST="http://192.168.1.205:8081/AI_BD/";
Vue.prototype.$axios = axios;
Vue.prototype.$store=store;
//引入表单验证
/* eslint-disable no-new */

//过滤器生成
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

const vue=new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



