import Vue from 'vue'
import Vuex from 'vuex'
import { stat } from 'fs';
import user from './user'
import state from './state'
import page from './page'
Vue.use(Vuex)
const store = new Vuex.Store({
        modules:{
                user,
                state,
                page
        }
})
export default store;



