
import publiccall from '../publicCall'
import { stat } from 'fs';
/*定义数据*/
const state = {
    user: {},
    userPage:[],
    islogin:false,
    userPerms:[],
  }
  //定义方法
  const mutations = {
    UPDATE_USER:(state, user)=>{
      state.user = user;
      sessionStorage.setItem("user_info", JSON.stringify(state.user));
    },
   SET_IS_LOGIN:(state,status)=>{
            state.islogin=status;
            sessionStorage.setItem("isLogin",state.islogin);
    },
    UPDATE_USERPAGE:(state,userpage)=>{
      state.userPage=userpage;
      sessionStorage.setItem("user_page",JSON.stringify(state.userPage));
    },
    REMOVE_USER:()=>{
      sessionStorage.clear();
      // sessionStorage.removeItem("user_info");
      // sessionStorage.removeItem("user_page");
    },
    UPDATE_USERPERMS:(state,userPerms)=>{
      state.userPerms=userPerms;
      sessionStorage.setItem("user_perms",JSON.stringify(state.userPerms));
    }
  }
  const getters = {
    GETUSERPAGE:(state)=>{
      return state.userPage;
    },
    GETUSERPERMS:(state)=>{
      return state.userPerms;
    }
  }
  const actions={
          IS_LOGIN:({commit})=>{
                  if (publiccall.isEmptyObject(state.user)) {
                          state.user = JSON.parse(sessionStorage.getItem('user_info'))
                          state.islogin=sessionStorage.getItem("isLogin");
                          state.userPage=JSON.parse(sessionStorage.getItem("user_page"));
                          state.userPerms=JSON.parse(sessionStorage.getItem("user_perms"));
                  }
          }
  }
  const store = {
    state,
    getters,
    actions,
    mutations
  }
  export default store;


