//权限控制
import Vue from 'vue'
import router from './router'
import store from './store'
import publiccall from './publicCall'
// 全局导航钩子

router.beforeEach((to, from, next) => {
  store.commit("UPDATESTATEIDANDTITLE", {
    id: to.name,
    title: to.meta.title
  });
  // 判断该路由是否需要登录权限
  if (to.meta.requireAuth) {
    if (!store.state.islogin) {
      store.dispatch('IS_LOGIN')
    }
    // 通过vuex state获取当前的token是否存在
    if (!publiccall.isEmptyObject(store.state.user.user) && store.state.user.islogin) {
      var userpage = store.state.user.userPage;
      var index = 0;
      if (to.fullPath == '/main') {
        next();
      } else {
        for (var i = 0; i < userpage.length; i++) {
          for (var j = 0; j < userpage[i].list.length; j++) {
            if ('/main/' + userpage[i].list[j].pageInf.url == to.fullPath) {
              index += 1;
            }
          }
        }
        if (to.fullPath == '/userindex') {
          index = 1;
        }
        if (index > 0) {
          next();
        } else {
          Vue.prototype.$layer.alert('前往网页不存在或角色权限不足', { icon:1,time: 2000, //2s后自动关闭
            end: function () {
              next({
                path: '/main'
              });
            }
          })

        }
      }
    } else {
      Vue.prototype.$layer.alert('帐号未登录', {icon:1,time: 2000, //2s后自动关闭
        end: function () {
          next({
            path: '/',
            //  query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
          });
        }
      })
      store.commit("SET_IS_LOGIN", false);
      store.commit("REMOVE_USER");
    }
  } else {
    next();
  }
})



