import Vue from "vue";

const state={
   stateid:"",
   statetitle:""
}
const mutations={
    UPDATESTATEID:(state,value)=>{
        state.stateid=value;
    },
    UPDATESTATEIDANDTITLE:(state,data)=>{
        state.stateid=data.id;
        state.statetitle=data.title;
    }
}
const actions={
}
const getters={
    GETUSERSTATE:(state)=>{
        return state;
    }
}
 const Rouerstate={
     state,
     mutations,
     getters
 }

 export default Rouerstate;


